import { Float, OrbitControls } from "@react-three/drei";
import { Canvas, useThree, useFrame, useLoader } from "@react-three/fiber";
import { Suspense, useState } from "react";
import { TextureLoader } from "three";
import gsap from "gsap";

import Effects from "./Effects";
import Lights from "./Lights";
import Model from "./Model";
import TextMesh from "./TextMesh";
import Carousel from "./Carousel";
import ImageMesh from "./ImageMesh";

const Camera = ({scroll, setAnimationFactor, setBgColor}) =>{
    useFrame((state, delta) =>
    {
        // gsap.to(state.camera.position, {duration:0.1, x:Math.sin(scroll) * 12})
        // state.camera.lookAt(0, 0, 0)
        
        if(scroll <= 0.9){
            gsap.to(state.camera.rotation, {duration:0.7, z:0})
        }
        else if(scroll > 0.9 && scroll <= 1.9 ){
            gsap.to(state.camera.rotation, {duration:0.7, z:0})
            gsap.to(state.camera.position, {duration:0.7, z:5, x:0})
        }
        else if(scroll > 1.9 && scroll <= 2.9 ){
            setAnimationFactor(1)
            // gsap.to(state.camera.rotation, {duration:0.7, z:-Math.PI / 2})
            // gsap.to(state.camera.position, {duration:0.7, z:3, x:-2})
        }
        else if(scroll > 2.9 && scroll <= 3.9 ){
            setAnimationFactor(0)
            // gsap.to(state.camera.rotation, {duration:0.7, z:Math.PI})
        }
        else if(scroll > 3.9 && scroll <= 4.9 ){
            setAnimationFactor(0)
        }
    // if(scroll>3 && scroll<4){
    //     setAnimationFactor(0)
    //     gsap.to(state.camera.position, {duration:0.3, z:-5})
    // }
    // else{
    //     setAnimationFactor(1)
    //     gsap.to(state.camera.position, {duration:0.3, z:2})
    // }

    })

    return(
        null
    )
}

const Experience = ({ count = 80, scroll, bgColor, setBgColor, position, meshVisible, textureIndex }) =>
{
    // Set for animation factor (0 for total stop, 1 for normal speed)
    const [animationFactor, setAnimationFactor] = useState(1)

    const t1 = useLoader(TextureLoader, "./images/2.jpg")

    return(
        <Canvas gl={{ alpha: false }} camera={ { near:0.01, far:110 } }>
            <color attach="background" args={[bgColor]} />

            <Suspense fallback={null}>
                {Array.from( {length: count}, (_,i) => <Model scale={0.5} key={i} z={-i} animationFactor={animationFactor} />)}
            </Suspense>

            <ImageMesh texture={t1} position={position} meshVisible={meshVisible} textureIndex={textureIndex} />

            {/* <TextMesh text="ANTINO" size={25} position={[0, 0, -25]} rotation={[0, 0, 0]} color="#000000" outlineColor="#000000" /> */}

            <Lights bgColor={bgColor} />
            {/* <Effects /> */}

            <Camera scroll={scroll} setAnimationFactor={setAnimationFactor} setBgColor={setBgColor} />
        </Canvas>
    )
}

export default Experience