import { useEffect, useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import {HelmetProvider} from 'react-helmet-async'
import './App.css';


import Home from './components/Home/Home';
import Experience from './three/Experience';
import About from './components/About/About';
import Contact from './components/Contact/Contact';

const App = () =>
{
  const [scroll, setSCroll] = useState(0)
  const [sidebarIsOpen, setSidebarIsOpen] = useState(false)
  const [bgColor, setBgColor] = useState('#ffffff')
  const [position, setPosition] = useState({})
  const [meshVisible, setMeshVisible] = useState(false)
  const [textureIndex, setTextureIndex] = useState(0)

  useEffect(()=>{
    window.addEventListener('scroll', ()=>{
      setSCroll(window.scrollY / window.innerHeight)
    })
  },[])

  // Effect hook to get mouse position
  useEffect(()=>
  {
      // Object to store mouse coords
      let coords = { x: 0, y: 0 }

      // Event listener for mousmove
      window.addEventListener('mousemove', (e)=>{
          coords.x = (e.clientX / window.innerWidth) - 0.5
          coords.y = (e.clientY / window.innerHeight) -0.5 
          setPosition(coords)            
      })
  }, [])

  return (
    <HelmetProvider>
      <div className="App">
        {/* <BrowserRouter>
          <Routes>
            <Route path='/' element={<Home 
                                      scroll={scroll} 
                                      sidebarIsOpen={sidebarIsOpen} 
                                      setSidebarIsOpen={setSidebarIsOpen} 
                                      setBgColor={setBgColor} 
                                      setMeshVisible={setMeshVisible}
                                      setTextureIndex={setTextureIndex}
                                    />} 
            />
            <Route path='/about' element={<About sidebarIsOpen={sidebarIsOpen} setSidebarIsOpen={setSidebarIsOpen} />} />
            <Route path='/contact' element={<Contact sidebarIsOpen={sidebarIsOpen} setSidebarIsOpen={setSidebarIsOpen} />} />
          </Routes>
        </BrowserRouter> */}

        <div className="canvas">
          <Experience 
            scroll={scroll} 
            bgColor={bgColor} 
            setBgColor={setBgColor} 
            position={position} 
            meshVisible={meshVisible} 
            setMeshVisible={setMeshVisible}
            textureIndex={textureIndex}
          />
        </div>  
      </div>
    </HelmetProvider>
  );
}

export default App;
