import glsl from 'babel-plugin-glsl/macro'
import { shaderMaterial } from '@react-three/drei';
import * as THREE from 'three'

const ImageShaderMaterial = shaderMaterial(
    {
        uTime: 0,
        uTexture: null,
        uAmplifier1: 1,
        uAmplifier2: 0.4,
        uColor: 1,
        uOpacity: 1,
        uNegator: 1
    },
    glsl`
    #pragma glslify: snoise3 = require(glsl-noise/simplex/3d);

    uniform float uTime;
    uniform float uAmplifier1;
    uniform float uAmplifier2;
    uniform float uFreq;

    attribute float aRandoms;

    varying vec2 vUv;
    varying vec3 vNormal;
    varying float vWave;

    void main(){
        vec3 pos = position;
        vec3 noisePos = vec3( pos.x + uTime * 0.5, pos.y, pos.z);
        pos.z += abs(snoise3(noisePos) * uAmplifier2);
        pos.z += abs(snoise3(noisePos) * uAmplifier2);
        pos.x += snoise3(noisePos) * 0.0;

        vec4 modelPosition = modelMatrix * vec4(pos,1.0);
    
        vec4 viewPosition = viewMatrix * modelPosition;
        vec4 projectionPosition = projectionMatrix * viewPosition;
        gl_Position = projectionPosition;

        vUv = uv;
        vNormal = normal;
        vWave = pos.z;
    }`,
    glsl`
    uniform float uTime;
    uniform sampler2D uTexture;
    uniform float uColor;
    uniform float uOpacity;
    uniform float uNegator;
    uniform float uAmplifier1;
    
    varying vec2 vUv;
    varying float vWave;
    
    void main()
    {
        float wave = vWave * 0.1;

        vec4 t = texture2D(uTexture, vUv);
    
        t.r *= 1.0;
        t.g *= 1.0;
        t.b *= 1.0;
    
        gl_FragColor = t;
        gl_FragColor.a = uOpacity;

        #include <tonemapping_fragment>
        #include <encodings_fragment>
    }`
)

export { ImageShaderMaterial }