import React, { useRef, useState } from 'react'
import { useThree, useFrame } from '@react-three/fiber'
import * as THREE from 'three'
import { useGLTF } from '@react-three/drei'
import gsap from 'gsap'

const Model = (props) =>
{
    const ref = useRef()

    const { nodes, materials } = useGLTF('/models/toy.glb')
    const { viewport, camera } = useThree()
    const { width, height } = viewport.getCurrentViewport( camera, [0, 0, props.z])

    const [pos] = useState({
        x: (THREE.MathUtils.randFloatSpread(2)),
        y: THREE.MathUtils.randFloatSpread( height ),
        z: 0,
        rX: Math.random() * Math.PI * 2,
        rY: Math.random() * Math.PI * 2,
        rZ: Math.random() * Math.PI * 2,
    })

    useFrame((state)=>{
        ref.current.rotation.set((pos.rX += 0.01 * props.animationFactor), (pos.rY += 0.01 * props.animationFactor), (pos.rZ += 0.01 * props.animationFactor))

        ref.current.position.set(pos.x * width, ( pos.y += 0.05 * props.animationFactor), props.z)

        if(pos.y > height * 1.5){
          gsap.to(pos, {duration:0.1, y: -height * 1.5})
        }
    })
  return (
    <group {...props} dispose={null}>
    <group rotation={[-Math.PI / 2, 0, 0]} ref={ref} >
        <group rotation={[Math.PI / 2, 0, 0]}>
          <mesh geometry={nodes.StoneToy_Body_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_Body_01_aiStandardSurface1_0.geometry} material={materials.aiStandardSurface1} />
          <mesh geometry={nodes.StoneToy_Tooth_1_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_Tooth_2_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_Tooth_3_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.polySurface3_aiStandardSurface2_0.geometry} material={materials.aiStandardSurface2} />
          <mesh geometry={nodes.polySurface4_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.polySurface1_aiStandardSurface2_0.geometry} material={materials.aiStandardSurface2} />
          <mesh geometry={nodes.polySurface2_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_stone_1_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_stone_2_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_stone_3_01_lambert1_0.geometry} material={materials.lambert1} />
          <mesh geometry={nodes.StoneToy_stone_4_01_lambert1_0.geometry} material={materials.lambert1} />
        </group>
      </group>
    </group>
  )
}

export default Model

useGLTF.preload('/models/toy.glb')
