import * as THREE from 'three'
import { TextureLoader } from 'three'
import { useFrame, useThree, extend, useLoader } from '@react-three/fiber'
import gsap from 'gsap'
import { useEffect, useRef } from 'react'

import { ImageShaderMaterial } from './shaders/ImageShaderMaterial'
extend({ ImageShaderMaterial })

const ImageMesh = ({position, meshVisible, texture, textureIndex}) =>
{
    // Refs
    const meshRef = useRef()
    const materialRef = useRef()

    // Image textures
    const textures = [
        useLoader(TextureLoader, './images/1.jpg'),
        useLoader(TextureLoader, './images/2.jpg'),
        useLoader(TextureLoader, './images/3.jpg'),
        useLoader(TextureLoader, './images/4.png'),
        useLoader(TextureLoader, './images/5.jpg')
    ]

    // Animate mesh each frame
    useFrame(({clock})=>
    {
        materialRef.current.uTime = clock.getElapsedTime()
        // materialRef.current.uTexture = textures[texture]
        if(meshRef.current)
        {
            // gsap.to(meshRef.current.position, {duration:0.1, x: position.x, y: -position.y, z: 2})

            if(meshVisible){
                meshRef.current.visible = true
                gsap.to(materialRef.current, {duration: 0.8, uOpacity:1})
                gsap.to(materialRef.current, {duration: 0.8, uAmplifier2:0.05})
            } 
            else{
                gsap.to(materialRef.current, {duration: 0.8, uOpacity:0})
                gsap.to(materialRef.current, {duration: 0.8, uAmplifier2:5})
                meshRef.current.visible = false
            }
        }
    })

    return(
        <mesh
            ref={meshRef}
            position={[0, 0, 2]}
            rotation={[0, 0, 1]}
        >
            <planeBufferGeometry args={ [11.4 * 0.8, 6.48 * 0.8,1,32,32] } />
            <imageShaderMaterial uTexture={textures[textureIndex]} side={THREE.DoubleSide} toneMapped={false} ref={materialRef} uOpacity={0.2} />
        </mesh>
    )
}

export default ImageMesh